body {
  margin: 0;
  font-family: 'Oswald', sans-serif;
  background-color: white;
  max-width: 100vw; }

.logo {
  color: #709255;
  background-color: #172815;
  padding: 0; }

.treeList {
  list-style: none; }

/* header */
.header {
  background-color: #709255;
  width: 100%;
  overflow: hidden;
  z-index: 3;
  position: relative; }

.header ul {
  margin: 0;
  padding: 0;
  list-style: none; }

.header li a {
  display: block;
  padding: 20px 20px;
  color: white;
  background-color: #709255;
  text-decoration: none; }

.header li a:hover,
.header .menu-btn:hover {
  background-color: white;
  color: #3E5622; }

.header .logo {
  display: block;
  float: left;
  font-size: 2em;
  padding: 10px 20px;
  text-decoration: none; }

/* menu */
.header .menu {
  clear: both;
  max-height: 0;
  transition: max-height .2s ease-out;
  max-width: 100%; }

/* menu icon */
.header .menu-icon {
  cursor: pointer;
  display: inline-block;
  float: right;
  padding: 28px 20px;
  position: relative;
  user-select: none; }

.header .menu-icon .navicon {
  background: #172815;
  display: block;
  height: 2px;
  position: relative;
  transition: background .2s ease-out;
  width: 18px; }

.header .menu-icon .navicon:before,
.header .menu-icon .navicon:after {
  background: #172815;
  content: '';
  display: block;
  height: 100%;
  position: absolute;
  transition: all .2s ease-out;
  width: 100%; }

.header .menu-icon .navicon:before {
  top: 5px; }

.header .menu-icon .navicon:after {
  top: -5px; }

/* menu btn */
.header .menu-btn {
  display: none; }

.header .menu-btn:checked ~ .menu {
  max-height: 100%; }

.header .menu-btn:checked ~ .menu-icon .navicon {
  background: transparent; }

.header .menu-btn:checked ~ .menu-icon .navicon:before {
  transform: rotate(-45deg);
  background: #3E5622; }

.header .menu-btn:checked ~ .menu-icon .navicon:after {
  transform: rotate(45deg);
  background: #3E5622; }

.header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:before,
.header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:after {
  top: 0; }

.banner {
  min-width: 100%;
  max-width: 100%;
  height: 500px;
  object-fit: cover; }

.adminBanner {
  min-width: 100%;
  max-width: 100%;
  height: 100px;
  object-fit: cover; }

.footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 10px;
  background-color: #172815;
  color: #95B46A; }

.mapContain {
  padding-top: 70px;
  width: 100%;
  height: 500px;
  text-align: center; }

iframe {
  height: 100%;
  width: 100%; }

.adminnav {
  list-style-type: none;
  text-align: center; }

.adminnav a {
  color: green; }

/* 48em = 768px */
@media (min-width: 48em) {
  .header li {
    float: left; }
  .header li a {
    padding: 20px 30px; }
  .header {
    width: 100%;
    position: fixed; }
  .header .menu {
    clear: none;
    max-height: none; }
  .header .menu-icon {
    display: none; } }
